import './index.scss';
import HeadshotImg from '../../assets/images/headshot.jpg';

const Headshot = () => {
    return (
        <div className='headshot-container'>
            <img className='solid-headshot' src={HeadshotImg} alt='headshot'/>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 620 700">
                <line className="line" stroke="#8BD8BD" stroke-width="24" x1="0" x2="300" y1="688" y2="688"/>
                <line className="line" stroke="#8BD8BD" stroke-width="24" x1="12" x2="12" y1="700" y2="450"/>
                <line className="line" stroke="#8BD8BD" stroke-width="24" x1="620" x2="420" y1="12" y2="12"/>
                <line className="line" stroke="#8BD8BD" stroke-width="24" x1="608" x2="608" y1="0" y2="250"/>
            </svg>
        </div>
    );
}

export default Headshot;